import React, {Component} from "react";
import { IonCol, IonItem } from "@ionic/react";
import { IonContent, IonHeader, IonPage, IonTitle, IonList, IonLabel, IonFabButton,IonRadioGroup, IonRadio, IonRange,  IonInput } from '@ionic/react';

import click1 from "../audio/click1.wav";
import click2 from "../audio/click2.wav";
import bleep1 from "../audio/bleep1.wav";
import bleep2 from "../audio/bleep2.wav";
import drum1 from "../audio/drum1.wav";
import drum2 from "../audio/drum2.wav";
import "./metronome.css";
import ci from 'correcting-interval';



class Metronome extends Component {
  constructor(props) {
    super(props);

    this.state = {
      playing: false,
      count: 0,
      bpm: 120,
      beatsPerMeasure: 4,
      click: "Bleep"
    };

    this.click1 = new Audio(click1);
    this.click2 = new Audio(click2);
    this.bleep1 = new Audio(bleep1);
    this.bleep2 = new Audio(bleep2);
    this.drum1 = new Audio(drum1);
    this.drum2 = new Audio(drum2);

    this.startStop = this.startStop.bind(this);
    this.playClick = this.playClick.bind(this);
    this.handleBpmChange = this.handleBpmChange.bind(this);
    this.handleTimeChange = this.handleTimeChange.bind(this);
    this.handleClickChange = this.handleClickChange.bind(this);
  }

  startStop() {
    if (this.state.playing) {
      //stops timer
      ci.clearCorrectingInterval(this.timer);
      this.setState({
        playing: false
      });
    } else {
      //starts timer with current bpm
      var that = this;
      this.timer = ci.setCorrectingInterval(function() {
        that.playClick();
      },
        (60 / this.state.bpm) * 1000
      );
      this.setState(
        {
          count: 0,
          playing: true
          //plays a click "immediately" (after setState finishes)
        },
        this.playClick
      );
    }
  };

  playClick() {
    const { count, beatsPerMeasure } = this.state;
    //The first beat will have a different sound that the others
    if (count % beatsPerMeasure === 0) {
      switch (this.state.click) {
        case "Click":
          this.click2.play();
          break;
        case "Bleep":
          this.bleep2.play();
          break;
        case "808":
          this.drum2.play();
          break;
        default:
          this.drum2.play();
      }
    } else {
      switch (this.state.click) {
        case "Click":
          this.click1.play();
          break;
        case "Bleep":
          this.bleep1.play();
          break;
        case "808":
          this.drum1.play();
          break;
        default:
          this.drum1.play();
      }
    }
    //Keep track of which beat we're on
    this.setState(state => ({
      count: (state.count + 1) % state.beatsPerMeasure
    }));
  };

  handleBpmChange(event) {
    const bpm = event.target.value;

    if (this.state.playing) {
      //stop the old timer and start a new one
      var that = this;
      ci.clearCorrectingInterval(this.timer);
      this.timer = ci.setCorrectingInterval(() => {
        that.playClick();
      },
        (60 / this.state.bpm) * 1000
      );
      //Set the new bpm, and reset the beat counter
      this.setState({
        count: 0,
        bpm
      });
    } else {
      //otherwise just update the bpm
      this.setState({ bpm });
    }
  };

  handleTimeChange(event) {
    if (event.target.value <= 20 && event.target.value >= 1) {
      this.setState({
        beatsPerMeasure: event.target.value
      });
    } else {
      //disableInput should eliminate the need for this
      alert("Please select a value between 1 & 20");
      this.setState({
        beatsPerMeasure: 4
      });
    }
  };

  handleClickChange(event) {
      console.log(event.target.value)
    this.setState({
      click: event.target.value
    });
  };

  disableInput(event) {
    event.preventDefault();
    event.stopPropagation();
  };

  componentWillUnmount() {
    ci.clearCorrectingInterval(this.timer);
    this.setState({
      playing: false,
      count: 0,
      bpm: 120,
      beatsPerMeasure: 4,
      click: "Click"
    });
  };

  render() {
    const { playing, bpm, click } = this.state;

    return (
    <IonPage>
      <IonContent className="metronome">
          <IonHeader className="metHead" style={{backgroundColor: "#2B547E", height: "60px"}}>
          <IonTitle style={{fontSize: "40px", color:"white", marginLeft: "500px"}}>Metronome Beats</IonTitle>  
          </IonHeader>
         <IonList className="time-signature">
          <IonItem >
            <IonLabel style={{ fontSize: "25px", color:"white", marginLeft: "25px"}}>{this.state.beatsPerMeasure}/4 Time</IonLabel>
            <IonInput style={{ fontSize: "25px", color:"white"}}
              type="number"
              min="1"
              max="20"
              onKeyDown={this.disableInput}
              onIonChange={this.handleTimeChange}
             />
          </IonItem>
         </IonList>
        <br />
        <IonList className="bpm-slider">
          <IonCol>
            <IonLabel style={{ fontSize: "25px", color:"white", marginLeft: "30px"}}>{bpm} BPM</IonLabel>
            <IonRange style={{ fontSize: "25px", color:"white"}}
              type="range"
              min="60"
              max="240"
              value={bpm}
              onIonChange={this.handleBpmChange}
            />
            <IonFabButton onClick={this.startStop} style={{ fontSize: "19px", color:"white", marginLeft: "30px"}}>
              {playing ? "Stop" : "Start"}
            </IonFabButton>
          </IonCol>
        </IonList>
        <br />
        <IonList className="click-type">
          <IonRadioGroup value={click} onIonChange={(e)=>this.handleClickChange(e)}>
            <IonLabel style={{ color:"white", marginLeft: "30px", fontSize: "25px"}}>{click}</IonLabel>
            <IonItem className="click-input">
              <IonLabel style={{ color:"white", marginLeft: "30px", fontSize: "25px"}}>Click</IonLabel>
              <IonRadio
                type="radio"
                value="Click"
                checked={this.state.click === "Click"}
                
              />
              <IonLabel style={{ color:"white", marginLeft: "30px", fontSize: "25px"}}>Bleep</IonLabel>
              <IonRadio
                type="radio"
                value="Bleep"
                checked={this.state.click === "Bleep"}
                
              />
              <IonLabel style={{ color:"white", marginLeft: "30px", fontSize: "25px"}}>808</IonLabel>
              <IonRadio
                type="radio"
                value="808"
                checked={this.state.click === "808"}
                
              />
            </IonItem>
            </IonRadioGroup>
        </IonList>
      </IonContent>
    </IonPage>
    );
  }
}

export default Metronome;
